  // top: 50%;
  // transform: translate(-50%, -52%);
  // width: auto;
}

.general-for-archive-title-price {
  h2 {
    font-size: 1rem;
    font-weight: 400;
    font-family: 'Merriweather Web', serif;
    letter-spacing: 0;
    line-height: 1.75;
    padding-right: 0.25rem;
    text-transform: none;
    background: #fff;
    display: inline;
  }

  a {
    text-decoration: none;
    font-family: 'Merriweather Web', serif;
    color: #2f1339;
  }
}
